import './App.css';

// import ClickCounter from './ClickCounter';
// import Users from './Users';

import { Route, Switch, Link, Redirect } from 'react-router-dom';

import Home from './Home';
import Projects from './Projects';
import Project from './Project';
import ClickCounter from './ClickCounter';
import Users from './Users';

import UserDataForm from './UserDataForm';
import FileInput from './FileInput';

function App() {
  return (
    <div className="App">
      {/* <p>
        The App ('root') component is running.
      </p>
      <hr />
      <ClickCounter />
      <hr />
      <Users />
      <br /><br /> */}
      <header>
        {/* Header */}
        <Link to='/'>Home</Link> |&nbsp; 
        <Link to='/projects'>Projects</Link> |&nbsp;  
        <Link to='/projects?page=1&amp;perPage=10'>Projects (query)</Link> |&nbsp; 
        <Link to='/project/55'>Project (55)</Link> |&nbsp;  
        <Link to='/clickcounter'>ClickCounter</Link> |&nbsp;  
        <Link to='/users'>Users</Link> |&nbsp;  
        <Link to='/xyz123'>/xyz123</Link> |&nbsp; 
        <Link to='/UserDataForm'> (Week 5) UserDataForm</Link> |&nbsp; 
        <Link to='/FileInput'>FileInput</Link>
      </header>

      <Switch>
          <Route exact path='/' render={() => (
              <Home />
          )}/>
          <Route exact path='/Projects' render={(props) => (
              <Projects  query={props.location.search} />
          )}/>
          <Route path='/Project/:id' render={(props) => (
              <Project id={props.match.params.id} />
          )}/>
          <Route exact path='/clickcounter' render={() => (
              <ClickCounter />
          )}/>
          <Route exact path='/users' render={() => (
              <Users />
          )}/>
          <Route exact path='/userdataform' render={() => (
              <UserDataForm />
          )}/>
          <Route exact path='/fileinput' render={() => (
              <FileInput />
          )}/>
          <Route render={() => (
              <Redirect push to={"/"} />
              // <h1>Not Found</h1>
          )}/>


      </Switch>

      <footer>Footer</footer>

    </div>
  );
}

export default App;
