import React, {useState} from 'react';
import './Users.css';

function Users(props){
    const [users, setUsers] = useState([
        { 'user': 'fred',    'active': false, 'age': 40 },
        { 'user': 'pebbles', 'active': false, 'age': 1  },
        { 'user': 'barney',  'active': true,  'age': 36 }
    ]);
    

    // // define a state 'loading' for using the code here:
    // if(!loading){
    //     return <p>Done Loading! - TODO: Show the data here</p>;
    // }else{
    //     return null; // don't render anything - still loading
    // }
    
    return (
        <div className="Users">
            <h2>Rendering Data</h2>

            <h4>Logical &amp;&amp; Operator (If)</h4>
            {users[0].active &&
                <p>{users[0].user} is Active!</p>
            }
            <br />
            {users[2].active &&
                <p>{users[2].user} is Active!</p>
            }

            <h4>Ternary Operator (If-Else)</h4>
            {users[0].active ? (
                <p>{users[0].user} is Active!</p>
            ) : (
                <p>{users[0].user} is Inactive!</p>
            )}
            {users[2].active ? (
                <p>{users[2].user} is Active!</p>
            ) : (
                <p>{users[2].user} is Inactive!</p>
            )}

            <h4>Rendering Array/Collection data using Array.map() (Iteration)</h4>
            <table>
                <thead>
                    <tr>
                        <th>User</th>
                        <th>Active</th>
                        <th>Age</th>
                    </tr>
                </thead>
                <tbody>
                    {/* {users.map(user => 
                        <tr> */}
                    {users.map((user, index) => 
                        <tr key={index}>
                            <td>{user.user}</td>
                            <td>{(user.active) ? "yes" : "no"}</td>
                            <td>{user.age}</td>
                        </tr>
                    )}

                </tbody>
            </table>

            <h4>Returning Null</h4>
            <p>See the commented code above return statement</p>
        </div>
    );
    
}

export default Users;