import React, {useState} from 'react';

function ClickCounter(props){
    const [numClicks, setNumClicks] = useState(0);

    function increaseNumClicks(e, msg){ // 'e' is the current event object
        console.log("passed-in message: ", msg)
        setNumClicks(prevClicks => prevClicks + 1); // arrow function
        // setNumClicks((prevClicks) => { return prevClicks + 1 });
        // setNumClicks(numClicks + 1);
    }

    // return <button onClick={increaseNumClicks}>Clicks: {numClicks}</button>

    // passing parameters to the function
    return (
            <div>
                <h2>Handling Events</h2>
                <button onClick={(e)=>{increaseNumClicks(e, "Hello"+' '+numClicks)}}>Clicks: {numClicks}</button>
            </div>
        );
    
}

export default ClickCounter;