import React, {useState} from 'react';
import queryString from 'query-string';

function Projects(props){
    // return <h1>All Projects Page - query: {props.query}</h1>
    return (
            <div>
                <h1>All Projects Page</h1>
                <p>Query string: {props.query}</p>
                <ul>Object parsed from query string:  
                    <li>page: {queryString.parse(props.query).page}</li>
                    <li>perPage: {queryString.parse(props.query).perPage}</li>
                </ul>
            </div>
        );
}

export default Projects;
